<template>
    <div>
        <list-top :playList="state.playlist"></list-top>
    </div>
</template>
<script>
import { getPlayListDetail } from "@/assets/apifuntion";
import { onMounted, reactive } from "vue";
import { useRoute } from "vue-router";
import listTop from '@/components/listTop.vue'
import { useStore } from "vuex";
export default{
    name:'ListView',
    components:{
        listTop,
    },
    setup(){
        let route=useRoute();
        let store=useStore()
        let state=reactive({
            list:[],
            playlist:{
                creator:{},
                tracks:[
                    {
                        al:{
                            name:'',
                            picUrl:'1'
                        },
                        ar:[{
                                name:'',
                            },
                        ],
                        originSongSimpleData:{
                            albumMeta:{},
                        },
                    }
                ],
            },

        });
        onMounted(async ()=>{
            state.playlist= await (await getPlayListDetail(route.query.id)).data.playlist;
            console.log('state.playlist',state.playlist);
            store.commit('setPlayList',state.playlist.tracks)
            store.dispatch('reqMusic',store.state.playlist[store.state.playCurrentIndex]);
        });
        return{
            route,
            state,
        }
    }
}
</script>